.App {
  text-align: center;
}
body {
  background-color: #4f4dee;
}
.card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  margin: 3rem auto;
  border: 3px solid #000;
  border-radius: 20px;
  box-shadow: 1px 1px 1px 1px rgb(0, 0, 0, .5);
  background-color: #fff;
}

img {
  width: 50%;
  padding: 5px;
  border-radius: 10px;
}

